@charset "UTF-8";
.dropdown {
  width: 100%; }
  .dropdown .btn-brand {
    width: 100%;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    padding-left: 0.6em;
    padding-right: 0.6em; }
  .dropdown .dropdown-menu {
    min-width: 100%;
    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.2);
    cursor: default; }
  .dropdown .dropdown-item {
    padding-left: 2em;
    color: #555; }
    .dropdown .dropdown-item + .dropdown-item {
      border-top: 1px dashed rgba(0, 0, 0, 0.1); }
  .dropdown:not(.multiple) .dropdown-item.selected, .dropdown:not(.multiple) .dropdown-item:active {
    background: #4a90e2;
    color: #FFF;
    text-shadow: 0 1px 0 rgba(0, 0, 0, 0.3); }
    .dropdown:not(.multiple) .dropdown-item.selected .text-muted, .dropdown:not(.multiple) .dropdown-item:active .text-muted {
      color: rgba(255, 255, 255, 0.7) !important; }
  .dropdown:not(.multiple) .dropdown-item.selected:before {
    content: "✔";
    display: inline-block;
    width: 1.2em;
    margin-left: -1.2em;
    text-align: center; }
  .dropdown.multiple .dropdown-item i {
    display: inline-block;
    width: 1.5em;
    margin-left: -1.5em;
    text-align: center;
    color: #4a90e2; }
  .dropdown.multiple .dropdown-item:active {
    background: #f8f9fa;
    text-shadow: none;
    color: #555; }
    .dropdown.multiple .dropdown-item:active:before {
      display: none; }
